body {
  margin: 0;
  font-size: 1.125rem;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Roboto",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  position: fixed;
  width: 100vw;
  height: 3rem;
  z-index: 2;

  background-color: white;
  padding-top: 1rem;
  border: solid 0.15rem grey;
}

#logo {
  margin-left: 1rem;
}

/*The id `gdsc-banner` and `overlay` are the image and the darkish overlay resp. moving/resizing one would require doing the same for the
other too.*/

#gdsc-banner {
  margin-top: 4rem;
  margin-bottom: 2rem;
  height: 24rem;
  width: 100vw;
}

#topic {
  margin-top: 15rem;
}

#overlay {
  margin-top: 4rem;
  margin-bottom: 2rem;
  position: absolute;
  height: 24rem;
  width: 100vw;

  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  justify-content: center;
  font-size: 5rem;
  text-shadow: 0 0 1rem #121215;
  -webkit-text-stroke-width: 0.05rem;
  color: aliceblue;
}

#description {
  text-align: center;
}

#playground {
  width: 90vw;
  margin: auto;
  height: 60vh;
  padding-bottom: 20vh;
}

.badge {
  /* centering of this div was done by setting margin as half of viewport width after translating the div itself*/
  display: flex;
  transform: translate(-50%);
  justify-self: center;
  margin-left: 50vw;
  margin-top: 3rem;
  width: 30rem;
  height: 5rem;
  background-color: rgb(255, 255, 255);
  color: #121215;
  font-size: 1rem;
  padding-left: 1rem;
  padding-top: 1rem;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 1rem;
}

.progress {
  padding-left: 2rem;
  padding-right: 2rem;
  width: 20rem;
  text-align: center;
  justify-content: center;
}

p {
  font-size: 1.25rem;
}

#login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  transform: translate(-50%);
  margin-left: 50vw;
  margin-top: 10rem;
  padding: 3rem;
  height: 10rem;
  width: 20rem;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border: solid 1px rgba(0, 0, 0, 0.15);
  border-radius: 1rem;
  font-size: larger;
}

#info {
  margin-bottom: 1rem;
}

#login--form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 1rem;
}

#login--form input {
  font-size: 1rem;
}

#login--form button {
  margin-top: 0.75rem;
  padding: 10px 25px;
  font-size: 0.75rem;
  font-weight: bold;
}
